import * as React from "react";
import {useEffect, useState} from 'react';
import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";

function srcset(image, size, rows = 1, cols = 1) {
  return {
    src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
    srcSet: `${image}?w=${size * cols}&h=${
      size * rows
    }&fit=crop&auto=format&dpr=2 2x`,
  };
}

function resize(setWidth) {
  setWidth(window.innerWidth);
}

function importAll(r) {
  return r.keys().map(r);
}

const images = importAll(
  require.context("./photos/", false, /\.(png|jpe?g|svg)$/)
);

export default function QuiltedImageList() {
 
  
  const [theWidth, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    window.addEventListener('resize', () => resize(setWidth));
    return () => {
      window.removeEventListener('resize', () => resize(setWidth));
    };
  }, []);
  
  return (
    <ImageList
      sx={{ width: theWidth-50, margin: 2 }}
      variant="quilted"
      cols={4}
      rowHeight={400}
    >
      {itemData.map((item) => (
        <ImageListItem
          key={item.img}
          cols={item.cols || 1}
          rows={item.rows || 1}
        >
          <img
            {...srcset(item.img, 400, item.rows, item.cols)}
            alt={item.title}
            loading="lazy"
          />
        </ImageListItem>
      ))}
    </ImageList>
  );
}

